.footer-school {
  width: 100%;
  display: block;
  background: #E8EAF0;
  padding: 30px 0;
}
.footer-school .school-warp {
  width: 1200px;
  margin: 0 auto;
}
.footer-school .footer-d {
  float: left;
  margin-top: 50px;
  padding: 10px 50px 35px 0 ;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  border-right: 1px solid #c5cad9;
  color: rgba(125, 140, 169, 0.6);
}
.footer-school .footer-d .logo_d {
  display: block;
  margin-bottom: 10px;
  width: 200px;
}
.footer-school .footer-d .logo_d img {
  width: 198px;
  height: auto;
}
.footer-school .footer-right {
  float: right;
  padding: 40px 0 30px;
  width: 100%;
}
.footer-school .footer-right .li {
  padding: 0 20px;
  text-indent: 5px;
}
.footer-school .footer-right .li .h5 {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  color: #7D8CA9;
}
.footer-school .footer-right .li span {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #7D8CA9;
  display: block;
  margin: 8px 0;
}
.footer-school .footer-right .li span a {
  color: #7D8CA9;
}
.footer-school .footer-right .li .li-pic {
  float: left;
  text-align: center;
  padding: 0 17px 0 0;
  list-style: none;
}
.footer-school .footer-right .li .li-pic img {
  margin: 0 auto;
  width: 86px;
  height: 86px;
  text-align: center;
  display: block;
}
.footer-school .footer-right .li .li-pic:last-child {
  padding-right: 0;
}
